@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");

* {
  margin: 0;
  padding: 0;
  outline: none;
  border: none;
  text-decoration: none;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif !important;
}

/* !important untuk override css */

/* .dashPage {
    padding-left: 400px;
} */

.container {
  display: flex;
}

.sidebar {
  position: absolute;
  top: 0;
  bottom: 0;
  height: 100%;
  left: 0;
  background: #ffff;
  width: 60px;
  overflow: hidden;
  transition: width 0.2s linear;
  box-shadow: 0 20px 35px rgba(0, 0, 0, 0.1);

  /* Link di baca dengan tag "a" */

  a {
    position: relative;
    color: rgb(85, 83, 83);
    font-size: 14px;
    display: table;
    width: 300px;
    padding: 10px;
  }

  a:hover {
    background: #eee;
  }

  .logoMarshall {
    text-align: center;
    display: flex;
    transition: all 0.5s ease;
    margin: 10px 0 0 0;
  }

  .logoMarshall img {
    width: 30px;
    height: 20px;
    margin-left: 10px;
    margin-top: 6px;
    margin-right: 5px;
  }

  .logoMarshall span {
    font-weight: bold;
    padding-left: 15px;
    font-size: 18px;
    text-transform: uppercase;
    margin: 13px 0 0 -10px;
  }

  .fas {
    position: relative;
    width: 20px;
    height: 20px;
    top: -4px;
    font-size: 20px;
    text-align: center;
    margin: 10px 0 0 13px;
  }

  .fas-logout {
    position: relative;
    width: 20px;
    height: 20px;
    top: -4px;
    font-size: 20px;
    text-align: center;
    margin: 10px 0 0 13px;
    color: var(--itdri3);
  }

  .logout {
    position: absolute;
    bottom: 0;
  }
}

nav:hover {
  width: 280px;
  transition: all 0.5s ease;
}

nav:hover + .main {
  left: 290px;
  transition: all 0.5s ease;
}

.nav-item {
  position: relative;
  top: -10px;
  margin-left: 20px;
}

.nav-item-logout {
  position: relative;
  top: -10px;
  margin-left: 20px;
  color: var(--itdri3);
}

/* Main Dashboard */
.main {
  position: relative;
  display: flex;
  align-items: center;
  left: 70px;
  width: 90%;
  margin-top: 10px;
  transition: all 0.2s linear;
}

.div-btn-dashboard {
  padding-top: 10px;
  padding-bottom: 10px;

  .btn-review {
    margin-left: 130px !important;
  }

  .disable {
    height: 100hv;
    width: 300px;
    margin-left: 10px;
    padding-bottom: 3px;
    background: var(--itdri3);
    color: #ffff;
    border-radius: 10px;
    font-weight: bold;
    font-size: 13px;
    transition: 0.5s ease;
    cursor: pointer;

    .icon {
      margin-right: 8px;
      font-size: 9px;
      transition: 0.5s ease;
    }

    &:hover {
      background: var(--itdri1);
      color: var(--whiteColor);
      /* transform: translateX(4px); */
      height: 25px;

      .icon {
        color: var(--whiteColor);
        transform: translateX(-3px);
      }
    }
  }

  .active {
    height: 100hv;
    width: 300px;
    margin-left: 10px;
    background: var(--itdri1);
    color: var(--whiteColor);
    border-radius: 10px;
    font-weight: bold;
    font-size: 20px;
    transition: 0.5s ease;

    .icon {
      margin-right: 8px;
      font-size: 15px;
      transition: 0.5s ease;
    }
  }
}

.form-bg {
  height: 90vh;
  width: 1450px;
  margin: auto;
  border-radius: 10px;
  background: var(--itdri3);
  align-items: top;
  justify-content: center;
  display: flex;

  .form-isi {
    height: 87vh;
    width: 1430px;
    margin: auto;
    border-radius: 10px;
    /* background-image: url("../Dashboard/assets/pixel.png"); */
    background-color: var(--whiteColor);
    align-items: top;
    justify-content: center;
    display: flex;
  }
}
