.judulModul {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  font-size: 30px;
}

.full-content {
  display: flex;
  justify-self: center;
  margin-top: 20px;

  /* background: whitesmoke; */
  width: 1200px;
}

/* ganti style dropdown main content */

