/* ///////////////// */

.dropDown-referensi {
  width: 180px;
  height: 100hv;
  margin-top: 15px;
}

.main-content {
  margin-right: 150px;

  .judul-Content {
    margin-top: 10px;
    font-size: 20px;
    font-weight: 500;
    color: var(--itdri3);
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
  }
}

.optional-content {
  .judul-Content {
    margin-top: 10px;
    font-size: 20px;
    font-weight: 500;
    color: var(--itdri3);
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
  }
}

.inputText {
  width: 300px;
  height: 25px;
  padding: 12px 10px;
  margin: 5px 0;
  box-sizing: border-box;
  border: 3px solid #ccc;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  outline: none;
  display: flex;

  &&:focus {
    border: 3px solid #555;
  }
}

.span-items {
  font-size: 15px;
}

.container-addField {
  margin-left: 100px;
  align-items: center;

  .judul-Content {
    margin-top: 10px;
    font-size: 20px;
    font-weight: 500;
    color: var(--itdri3);
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
    position: absolute;
  }

  .scroll-container {
    margin-left: 20px;
  }

  .field-context {
    display: flex;
    margin-top: 60px;
    margin-bottom: 10px;

    .span-field {
      font-size: 15px;
    }

    .toggle-Ref {
      margin-left: 20px;
    }
  }
}

.required {
  color: red;
}

.modulBut {
  display: flex;
  align-items: center;

  margin-top: 40px;
  margin-left: 530px;
  height: 100hv;
  cursor: pointer;

  background: var(--itdri3);
  color: #ffff;
  padding: 0.8rem 3.5rem;
  border-radius: 10px;
  font-weight: 500;
  transition: 0.3s ease;

  &:hover {
    background: var(--itdri1);
    color: var(--whiteColor);
  }
}

.addBut {
  margin-left: 20px;
  margin-top: 10px;

  background: var(--itdri3);
  color: #ffff;
  padding: 0.8rem 3.5rem;
  border-radius: 10px;
  font-weight: 500;
  transition: 0.3s ease;

  &:hover {
    background: var(--itdri1);
    color: var(--whiteColor);
  }
}

