@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap");

* {
  padding: 100;
  margin: 0;
  outline: none;
  border: none;
  text-decoration: none;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}

/* Default Styling */
:root {
  --primaryColor: hsl(94, 55%, 56%);
  --hoverColor: hsl(93, 75%, 76%);
  --paleGreen: hsl(96, 75%, 89%);
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(0, 0%, 18%);
  --greyText: hsl(0, 0%, 75%);
  --bgColor: hsl(113, 11%, 85%);
  --inputColor: hsl(330, 12%, 97%);
  --bgOn: hsl(47, 98%, 49%);
  --itdri1: #0093ad;
  --itdri2: #134a6e;
  --itdri3: #1f2855;
  --itdri4: #706f6f;

  /* Font ang Typography */
  --biggestFontSize: 2.5rem;
  --h1FontSize: 1.5rem;
  --h2FontSize: 1.25rem;
  --h3FontSize: 1rem;
  --normalFontSize: 0.938rem;
  --smallFontSize: 0.813rem;
  --smallestFontSize: 0.75rem;
}

.grid {
  display: grid;
  align-items: center;
  gap: 1.5rem;
}

.flex {
  display: flex;
  align-items: center;
}

html {
  font-size: 75%;
}

a {
  text-decoration: none;
}

/* li {
  list-style: none;
} */

.icon {
  font-size: var(--h2FontSize);
  cursor: pointer;
}

img,
video {
  height: auto;
  width: 100%;
}

.message {
  display: none;
}

.showMessage {
  display: block;
  color: var(--whiteColor);
  padding: 10px;
  background: var(--itdri3);
  border-radius: 3px;
  text-align: center;
  margin-bottom: 10px;
}

.btn {
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 8px;
  padding: 0.8rem 1rem;
  color: var(--greyText);
}

.btn .icon {
  margin-left: 0.5rem;
}

.btn:hover {
  color: var(--primaryColor);
  background: var(--paleGreen);
}

/* Page Styling */
.loginPage {
  height: 100vh;
  width: 100%;
  margin: auto;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 7px 50px rgb(214, 223, 213);

  .container {
    height: 75vh;
    width: 60%;
    margin: auto;
    justify-content: space-between;
    border-radius: 10px;
    background: hsl(0, 0%, 91%);

    .videoDiv {
      position: relative;
      display: flex;
      flex-basis: 50%;
      width: 70%;
      height: 100%;
      padding: 0 1.5rem;
      text-align: center;
      justify-content: center;
      flex-direction: column;
      margin: auto;
      border-radius: 10px;
      overflow: hidden;

      video,
      .footerDiv {
        position: absolute;
      }

      video {
        height: 100%;
        width: 100%;
        object-fit: cover;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
      }

      .textDiv {
        position: relative;

        .title {
          color: var(--whiteColor);
          font-size: 35px;
          font-weight: 800;
        }

        p {
          color: var(--whiteColor);
          padding: 1rem;
          font-weight: 400;
        }
      }

      .footerDiv {
        bottom: 10px;
        padding: 0 1rem;
        height: 60px;
        background: rgba(255, 255, 255, 0.248);
        left: 10px;
        right: 10px;
        justify-content: space-between;
        backdrop-filter: blur(1px);
        border-radius: 10px;
      }

      .text {
        color: var(--whiteColor);
      }

      .btn {
        background: var(--itdri3);
        color: var(--whiteColor);
        padding: 0.8rem 1.5rem;
        border-radius: 10px;
        font-weight: 400;
        transition: 0.3s ease;

        &:hover {
          background: var(--itdri1);
          color: var(--whiteColor);
        }
      }
    }

    .formDiv {
      flex-basis: 50%;
      margin: auto;
      flex-direction: column;
      gap: 1.5rem;
      transform: translateY(-1rem);

      .headerDiv {
        text-align: center;
        padding: 1.5rem 0;

        img {
          width: 160px;
          height: 100px;
          padding-bottom: 0.5rem;
          margin-left: 1rem;
        }

        h3 {
          font-size: 25px;
          color: var(--blackColor);
          font-weight: 600;
        }
      }

      .form {
        gap: 1rem;

        .inputDiv {
          margin-top: -11px;

          .icon {
            color: var(--greyText);
          }

          input {
            background: none;
            outline: none;
            border: none;
            width: 200px;
          }

          label {
            color: var(--blackColor);
            font-weight: 400;
            font-size: 14px;
            padding: 0.5rem;
            display: block;
          }

          .input {
            gap: 0.5rem;
            padding: 1rem;
            background: var(--inputColor);
            border-radius: 10px;
          }
        }

        .btn {
          padding: 0.8rem;
          background: var(--itdri3);
          border-radius: 10px;
          color: var(--whiteColor);
          align-items: center;
          margin: auto;
          justify-content: center;
          font-size: 16px;
          width: 100%;
          gap: 0.5rem;
          transition: 0.3s ease;

          .icon {
            font-size: 25px;
            transition: 0.3s ease;
          }

          &:hover {
            background: var(--itdri1);
            color: var(--whiteColor);

            .icon {
              color: var(--whiteColor);
              transform: translateX(5px);
            }
          }
        }

        .forgotPassword {
          font-size: 13px;
          color: var(--blackColor);
          opacity: 0.7;
          text-align: center;

          a {
            text-decoration: underline;
            cursor: pointer;
            opacity: 1;
          }
        }
      }
    }
  }
}

